/* html */
html {
  height: 100%;
  font-size: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  color: #1a1a1a;
  font-family: "Noto Sans JP", "YuGothic", "Yu Gothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  font-size: 1rem;
  line-height: 1.8;
  letter-spacing: 0.04em;
  font-weight: normal;
}

img {
  max-width: 100%;
}

a {
  color: #0017C1;
  text-decoration: underline;
  transition: 0.1s;
}
a:where(:visited) {
  color: #6C006C;
}
a:where(:hover, :active, :focus) {
  color: #00118F;
}

/* wrapper */
.wrapper {
  position: relative;
  flex: 1;
  padding-top: 4rem;
}
@media screen and (max-width: 991.98px) {
  .wrapper {
    padding-top: 3rem;
  }
}

/*********************************
    コンテナ
*********************************/
.container {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #FFFFFF;
}
.container--breadcrum .container__inner {
  padding: 20px 20px 0 20px;
}
.container--cta-area .container__inner {
  padding: 0 20px 40px 20px;
}

.container__inner {
  display: flex;
  width: 100%;
  max-width: 1200px;
  padding: 20px 20px 40px 20px;
  gap: 40px;
}
@media screen and (max-width: 767.98px) {
  .container__inner {
    flex-direction: column;
  }
}

/*********************************
    ヘッダー
*********************************/
.header {
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  z-index: 30;
  background-color: rgba(255, 255, 255, 0.9490196078);
  box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.1), 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
}
@media screen and (max-width: 991.98px) {
  .header {
    height: 3rem;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
  max-width: 1200px;
  padding: 0 10px;
}

.header__title {
  line-height: 1;
  transition: 0.1s;
}
.header__title:hover, .header__title:active, .header__title:focus {
  opacity: 0.6;
}

.header__title-img {
  height: 24px;
  width: auto;
}
@media screen and (max-width: 991.98px) {
  .header__title-img {
    height: 20px;
  }
}

.header__nav--pc {
  height: 100%;
}
@media screen and (max-width: 991.98px) {
  .header__nav--pc {
    display: none;
  }
}
.header__nav--sp {
  position: absolute;
  top: 0;
  right: 0;
}
@media screen and (min-width: 992px) {
  .header__nav--sp {
    display: none;
  }
}

.header__hamburger {
  position: relative;
  display: block;
  width: 2.4rem;
  height: 2.4rem;
  cursor: pointer;
}
.header__hamburger--active .header__hamburger-line:nth-of-type(1) {
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%) rotate(-45deg);
}
.header__hamburger--active .header__hamburger-line:nth-of-type(2) {
  opacity: 0;
}
.header__hamburger--active .header__hamburger-line:nth-of-type(3) {
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%) rotate(45deg);
}
@media screen and (min-width: 992px) {
  .header__hamburger {
    display: none;
  }
}

.header__hamburger-line {
  position: absolute;
  display: inline-block;
  width: 60%;
  height: 2px;
  left: 20%;
  background: #1a1a1a;
  border-radius: 100px;
  transition: 0.3s;
}
.header__hamburger-line:nth-of-type(1) {
  top: 30%;
}
.header__hamburger-line:nth-of-type(2) {
  top: calc(50% - 1px);
}
.header__hamburger-line:nth-of-type(3) {
  top: calc(70% - 2px);
}

/*********************************
    フッター
*********************************/
.footer {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #e6e6e6;
}

.footer__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  padding: 40px 20px;
  gap: 40px;
}

.footer__menu {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
@media screen and (max-width: 767.98px) {
  .footer__menu {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.footer__left-column {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}
@media screen and (max-width: 767.98px) {
  .footer__left-column {
    flex-direction: column;
    align-items: stretch;
  }
}

.footer__sns-header {
  display: flex;
}
@media screen and (max-width: 767.98px) {
  .footer__sns-header {
    justify-content: center;
  }
}

.footer__sns-title {
  font-size: 1.125rem;
  line-height: 1.4;
  font-weight: 500;
}

.footer__sns-body {
  display: flex;
  gap: 10px;
}
@media screen and (max-width: 767.98px) {
  .footer__sns-body {
    justify-content: center;
  }
}

.footer__sns-link {
  transition: 0.1s;
}
.footer__sns-link:hover, .footer__sns-link:active, .footer__sns-link:focus {
  opacity: 0.6;
}

.footer__sns-logo {
  width: 36px;
  min-width: 36px;
  height: 36px;
}

.footer__right-column {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 40px;
}
@media screen and (max-width: 767.98px) {
  .footer__right-column {
    align-items: flex-start;
  }
}

.footer__nav {
  display: flex;
}

