.footer__banner {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  gap: 10px;
}
@media screen and (max-width: 767.98px) {
  .footer__banner {
    justify-content: flex-start;
  }
}

.footer__banner-link {
  transition: 0.1s;
}
.footer__banner-link:hover, .footer__banner-link:active, .footer__banner-link:focus {
  opacity: 0.6;
}

.footer__copyright {
  display: flex;
  justify-content: center;
  font-size: 0.75rem;
  line-height: 1.4;
}

/*********************************
    ページトップ
*********************************/
.pagetop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: max(40px, (100% - 1200px) / 2 - 40px);
  bottom: 20px;
  width: 40px;
  height: 40px;
  background-color: #000000;
  border-radius: 100px;
  z-index: 10;
  opacity: 0;
  transition: opacity 0.1s, background-color 0.1s;
}
.pagetop:hover, .pagetop:active, .pagetop:focus {
  opacity: 0.6;
}
.pagetop--show {
  opacity: 0.3;
}

.pagetop__icon {
  color: #FFFFFF;
  font-size: 20px;
}

/*********************************
    ページビジュアル
*********************************/
.page-visual {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 200px;
  background-color: #f2f2f2;
}

.page-visual__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
}

.page-visual__heading {
  font-size: 2.25rem;
  line-height: 1.4;
  font-weight: 500;
}

/*********************************
    コンテンツ
*********************************/
.content {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 40px;
}
@media screen and (max-width: 767.98px) {
  .content {
    order: 1;
  }
}

.content-box {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.1), 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
}

.content-box__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1000px;
  padding: 40px;
  gap: 40px;
}
@media screen and (max-width: 767.98px) {
  .content-box__inner {
    padding: 40px 20px;
  }
}

.content-box__header {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.content-box__header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content-box__header-category {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.content-box__header-date {
  font-size: 0.875rem;
  line-height: 1.4;
  white-space: nowrap;
}

.content-box__body {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.content-box__footer {
  display: flex;
  justify-content: center;
}

/*********************************
    サイドバー
*********************************/
.sidebar {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: 40px;
}
.sidebar--2column {
  width: 200px;
}
.sidebar--3column-left {
  width: 180px;
}
.sidebar--3column-right {
  width: 160px;
}
@media screen and (max-width: 767.98px) {
  .sidebar--2column {
    width: 100%;
    order: 2;
  }
  .sidebar--3column-left {
    width: 100%;
    order: 2;
  }
  .sidebar--3column-right {
    width: 100%;
    order: 3;
  }
}

.sidebar-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #cccccc;
  border-radius: 4px;
  padding: 20px;
  gap: 20px;
}

.sidebar-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar-menu {
  color: #1a1a1a;
  font-size: 1.125rem;
  line-height: 1.4;
  font-weight: 500;
  text-decoration: none;
  transition: 0.1s;
}
.sidebar-menu:hover, .sidebar-menu:active, .sidebar-menu:focus {
  color: rgba(26, 26, 26, 0.6);
}

.sidebar-date {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.sidebar-date__link {
  line-height: 1.4;
}

.sidebar-date__date {
  font-size: 0.75rem;
  line-height: 1.4;
}

.sidebar-ranking {
  position: relative;
  padding-left: 1em;
}
.sidebar-ranking::before {
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "";
  position: absolute;
  top: 0.15em;
  left: 0;
  font-size: 0.875rem;
}
.sidebar-ranking:nth-child(1)::before {
  content: "1";
}
.sidebar-ranking:nth-child(2)::before {
  content: "2";
}
.sidebar-ranking:nth-child(3)::before {
  content: "3";
}
.sidebar-ranking:nth-child(4)::before {
  content: "4";
}
.sidebar-ranking:nth-child(5)::before {
  content: "5";
}

.sidebar-ranking__link {
  line-height: 1.4;
}

.sidebar-filter {
  line-height: 1.4;
}

/*********************************
    見出し
*********************************/
