/*********************************
    ボタン
*********************************/
/* button */
.button {
  display: inline-block;
  width: fit-content;
  min-width: 150px;
  max-width: 100%;
  padding: 5px 15px;
  background-color: #666666;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #FFFFFF;
  font-family: "Noto Sans JP", "YuGothic", "Yu Gothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  font-size: 1.125rem;
  line-height: 1.4;
  letter-spacing: 0.04em;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  transition: 0.1s;
}
.button:hover, .button:active, .button:focus {
  background-color: #333333;
}
.button--secondary {
  background-color: #FFFFFF;
  border: 1px solid #666666;
  color: #666666;
}
.button--secondary:hover, .button--secondary:active, .button--secondary:focus {
  background-color: #e6e6e6;
}
.button--primary-small {
  min-width: auto;
  padding: 3px 9px;
  font-family: "Noto Sans JP", "YuGothic", "Yu Gothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  font-size: 0.875rem;
  line-height: 1.4;
  letter-spacing: 0.04em;
  font-weight: bold;
}
.button--secondary-small {
  min-width: auto;
  padding: 3px 9px;
  background-color: #FFFFFF;
  border: 1px solid #666666;
  color: #666666;
  font-family: "Noto Sans JP", "YuGothic", "Yu Gothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  font-size: 0.875rem;
  line-height: 1.4;
  letter-spacing: 0.04em;
  font-weight: bold;
}
.button--secondary-small:hover, .button--secondary-small:active, .button--secondary-small:focus {
  background-color: #e6e6e6;
}
.button--success {
  background-color: #259D63;
}
.button--success:hover, .button--success:active, .button--success:focus {
  background-color: #115A36;
}
.button--alert {
  background-color: #EC0000;
}
.button--alert:hover, .button--alert:active, .button--alert:focus {
  background-color: #850000;
}
.button--icon-after {
  position: relative;
  padding-right: 1.2rem;
}

.button__icon-after {
  position: absolute;
  top: 50%;
  right: 0.4rem;
  font-size: 0.875rem;
  transform: translateY(-50%);
}

/* button-group */
.button-group {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

/*********************************
    アイコン
*********************************/
.icon {
  display: inline-flex;
  align-items: center;
  gap: 5px;
}

.icon__icon {
  font-size: 0.875rem;
}

/*********************************
    分類ラベル
*********************************/
.class-label {
  display: inline-block;
  width: fit-content;
  padding: 3px 6px;
  background-color: #666666;
  border-radius: 2px;
  color: #FFFFFF;
  font-size: 0.625rem;
  line-height: 1.4;
  font-weight: bold;
}
.class-label--success {
  background-color: #259D63;
}
.class-label--alert {
  background-color: #EC0000;
}

/*********************************
    注釈
*********************************/
.annotation-text--alert {
  color: #EC0000;
}
.annotation-text--note {
  font-size: 0.75rem;
}

/*********************************
    ディバイダー
*********************************/
.divider {
  border-color: #b3b3b3;
}

/*********************************
    テーブル
*********************************/
.table--horizontal .table__header {
  width: 180px;
}
@media screen and (max-width: 767.98px) {
  .table--horizontal .table__header {
    width: auto;
  }
}
.table--vertical .table__body-row:first-child > * {
  border-top: none;
}
.table--cross .table__header-row:first-child > *:first-child {
  background-color: #FFFFFF;
  border-top: none;
  border-left: none;
}
.table--cross .table__body-row:first-child > * {
  border-top: none;
}
.table--sp-scroll {
  overflow-x: auto;
}
@media screen and (max-width: 767.98px) {
  .table--sp-scroll .table__inner {
    min-width: 900px;
  }
}
.table--sp-scroll .table__header, .table--sp-scroll .table__text {
  white-space: nowrap;
}

.table__inner {
  width: 100%;
  background-color: #FFFFFF;
  border-spacing: 0;
  border-collapse: separate;
}

.table__header-row:first-child > *, .table__body-row:first-child > * {
  border-top: 1px solid #666666;
}

.table__header {
  padding: 5px 10px;
  border: none;
  border-right: 1px solid #666666;
  border-bottom: 1px solid #666666;
  background-color: #f2f2f2;
  vertical-align: middle;
}
.table__header:first-child {
  border-left: 1px solid #666666;
}
.table__header--sp-sticky {
  position: sticky;
  left: 0;
  width: 180px;
}

.table__text {
  padding: 5px 10px;
  border: none;
  border-right: 1px solid #666666;
  border-bottom: 1px solid #666666;
  vertical-align: middle;
}
.table__text:first-child {
  border-left: 1px solid #666666;
}

/*********************************
    リスト
*********************************/
/* bullet */
.bullet-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.bullet-list__item {
  position: relative;
  padding-left: 0.8em;
  line-height: 1.4;
}
.bullet-list__item::before {
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f111";
  position: absolute;
  top: 0.7em;
  left: 0;
  font-size: 0.5em;
}

.child-bullet-list {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  gap: 10px;
}

.child-bullet-list__item {
  position: relative;
  padding-left: 0.8em;
  line-height: 1.4;
}
.child-bullet-list__item::before {
  font-family: "Font Awesome 6 Free";
  font-weight: 400;
  content: "\f111";
  position: absolute;
  top: 0.7em;
  left: 0;
  font-size: 0.5em;
}

/* order */
.order-list {
  counter-reset: order-list;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.order-list__item {
  position: relative;
  padding-left: 1.3em;
  line-height: 1.4;
}
.order-list__item::before {
  counter-increment: order-list;
  content: counter(order-list);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 1.6em;
  height: 1.6em;
  padding-left: 0.1em;
  background-color: #666666;
  border-radius: 100px;
  color: #FFFFFF;
  font-size: 0.625rem;
  line-height: 1.4;
}

.child-order-list {
  counter-reset: child-order-list;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  gap: 10px;
}

.child-order-list__item {
  position: relative;
  padding-left: 1.3em;
  line-height: 1.4;
}
.child-order-list__item::before {
  counter-increment: child-order-list;
  content: counter(child-order-list);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 1.6em;
  height: 1.6em;
  padding-left: 0.1em;
  background-color: #b3b3b3;
  border-radius: 100px;
  color: #FFFFFF;
  font-size: 0.625rem;
  line-height: 1.4;
}

/*********************************
    ページャー
*********************************/
.pager {
  width: fit-content;
}

.pager__inner {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
}

.pager__item:first-child > * {
  border-radius: 4px 0 0 4px;
}
.pager__item:last-child > * {
  border-radius: 0 4px 4px 0;
}

.pager__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: #666666;
  border: 1px solid transparent;
  color: #FFFFFF;
  line-height: 1.4;
  text-decoration: none;
  transition: 0.1s;
}
.pager__link:hover, .pager__link:active, .pager__link:focus {
  background-color: #333333;
}
.pager__link--active {
  background-color: #FFFFFF;
  border-color: #666666;
  color: #666666;
  pointer-events: none;
}

.pager__icon {
  font-size: 0.875rem;
}

/*********************************
    ポストリスト
*********************************/
/* news-posts */
.news-posts__link {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-bottom: 1px solid #b3b3b3;
  color: #1a1a1a;
  text-decoration: none;
  line-height: 1.4;
  gap: 10px;
  transition: 0.1s;
}
.news-posts__link:hover, .news-posts__link:active, .news-posts__link:focus {
  background-color: rgba(0, 0, 0, 0.0509803922);
}

.news-posts__header {
  display: flex;
  align-items: center;
  gap: 10px;
}

.news-posts__date {
  font-size: 0.875rem;
}

/* blog-posts */
.blog-posts__link {
  display: flex;
  padding: 10px;
  border-bottom: 1px solid #b3b3b3;
  color: #1a1a1a;
  text-decoration: none;
  line-height: 1.4;
  gap: 20px;
  transition: 0.1s;
}
.blog-posts__link:hover, .blog-posts__link:active, .blog-posts__link:focus {
  background-color: rgba(0, 0, 0, 0.0509803922);
}
@media screen and (max-width: 767.98px) {
  .blog-posts__link {
    flex-direction: column;
    gap: 10px;
  }
}

.blog-posts__img {
  width: 120px;
  height: fit-content;
  aspect-ratio: 1/1;
  object-fit: cover;
}

.blog-posts__body {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.blog-posts__title {
  font-size: 1.125rem;
  font-weight: 500;
}

.blog-posts__class-label {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.blog-posts__date {
  font-size: 0.875rem;
}

.blog-posts__text {
  font-size: 0.875rem;
  line-height: 1.8;
}

/*********************************
    メディア
*********************************/
.media {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media screen and (max-width: 767.98px) {
  .media {
    flex-direction: column;
    gap: 10px;
  }
}
.media--reverse {
  flex-direction: row-reverse;
}
@media screen and (max-width: 767.98px) {
  .media--reverse {
    flex-direction: column;
  }
}

.media__img {
  width: 30%;
  height: fit-content;
  aspect-ratio: 3/2;
  object-fit: cover;
}
@media screen and (max-width: 767.98px) {
  .media__img {
    width: 100%;
  }
}

.media__body {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1;
}

.media__text {
  font-size: 0.875rem;
}

.media__button {
  display: flex;
  justify-content: flex-end;
}

/*********************************
    カード
*********************************/
/* card-group */
.card-group {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
}
@media screen and (max-width: 767.98px) {
  .card-group {
    flex-direction: column;
  }
}
.card-group--column3 .card-group__item {
  width: calc((100% - 40px) / 3);
}
@media screen and (max-width: 767.98px) {
  .card-group--column3 .card-group__item {
    width: 100%;
  }
}
.card-group--column4 .card-group__item {
  width: calc((100% - 60px) / 4);
}
@media screen and (max-width: 767.98px) {
  .card-group--column4 .card-group__item {
    width: 100%;
  }
}

/* card */
.card {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  border: 1px solid #b3b3b3;
  border-radius: 4px;
  overflow: hidden;
}
.card--link {
  display: block;
  border: none;
  color: #1a1a1a;
  text-decoration: none;
  box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.1), 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
  transition: 0.1s;
}
.card--link:hover, .card--link:active, .card--link:focus {
  background-color: #f2f2f2;
}

.card__img {
  width: 100%;
  height: fit-content;
  aspect-ratio: 16/9;
  object-fit: cover;
}

.card__body {
  display: flex;
  flex-direction: column;
  padding: 20px;
  gap: 10px;
}

.card__title {
  font-size: 1.25rem;
  line-height: 1.4;
  font-weight: 500;
}

.card__text {
  font-size: 0.875rem;
}

/*********************************
    CTA
*********************************/
.cta-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px;
  gap: 20px;
  background-color: #f2f2f2;
  border-radius: 8px;
}
.cta-area--bg-none {
  background-color: transparent;
}

.cta-area__title {
  font-size: 1.5rem;
  line-height: 1.4;
}

.cta-area__divider {
  width: 100%;
  color: #b3b3b3;
}

/*********************************
    料金表
*********************************/
/* price-box-group */
.price-box-group {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
@media screen and (max-width: 767.98px) {
  .price-box-group {
    flex-direction: column;
  }
}

.price-box-group__item {
  flex: 1;
}

/* price-box */
.price-box {
  background-color: #FFFFFF;
  border: 1px solid #666666;
}

.price-box__header {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: #666666;
  color: #FFFFFF;
  line-height: 1.4;
  gap: 5px;
}

.price-box__title {
  text-align: center;
}

.price-box__price {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
}

.price-box__body {
  display: flex;
  flex-direction: column;
  padding: 10px;
  font-size: 0.875rem;
  gap: 10px;
}

.price-box__features {
  display: flex;
  flex-direction: column;
}

.price-box__features-header {
  padding: 5px;
  background-color: #e6e6e6;
  line-height: 1.4;
  text-align: center;
}

.price-box__features-text {
  padding: 5px;
  text-align: center;
}

/*********************************
    FAQ
*********************************/
.faq__item {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  border-bottom: 1px solid #b3b3b3;
  gap: 10px;
}
.faq__item:last-child {
  border-bottom: none;
}

.faq__row {
  position: relative;
  min-height: 40px;
  padding-left: 55px;
}

.faq__icon {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  line-height: 1;
  font-weight: bold;
}
.faq__icon--question {
  background-color: #666666;
  border: 1px solid transparent;
  color: #FFFFFF;
}
.faq__icon--answer {
  background-color: #FFFFFF;
  border: 1px solid #666666;
  color: #666666;
}

.faq__question-text {
  padding-top: calc((40px - 1.8rem) / 2);
  font-weight: 500;
}

.faq__answer-body {
  padding-top: calc((40px - 1.8rem) / 2);
}

/*********************************
    タブ
*********************************/
/* tab */
.tab__item {
  display: none;
  padding: 20px;
  animation: tabAnime 1s forwards;
}
.tab__item--active {
  display: block;
}

@keyframes tabAnime {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* tab-navigation */
.tab-navigation {
  display: flex;
}

.tab-navigation__item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-bottom: 4px solid #b3b3b3;
  line-height: 1.4;
  font-weight: 500;
  transition: 0.1s;
}
.tab-navigation__item:hover, .tab-navigation__item:active, .tab-navigation__item:focus {
  background-color: rgba(0, 0, 0, 0.0509803922);
  border-bottom-color: #666666;
}
.tab-navigation__item--active {
  background-color: rgba(0, 0, 0, 0.0509803922);
  border-bottom-color: #666666;
  pointer-events: none;
}

/*********************************
    アコーディオン
*********************************/
.accordion__button {
  position: relative;
  display: block;
  width: 100%;
  padding: 10px 35px 10px 10px;
  background-color: #e6e6e6;
  line-height: 1.4;
  font-weight: 500;
  transition: 0.1s;
}
.accordion__button::before {
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f107";
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-45%);
}
.accordion__button:hover, .accordion__button:active, .accordion__button:focus {
  background-color: #cccccc;
}
.accordion__button--active::before {
  transform: translateY(-55%) rotate(180deg);
}

.accordion__body {
  display: none;
  height: 0;
  padding: 10px;
  background-color: #FFFFFF;
  border-left: 1px solid #e6e6e6;
  border-right: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  gap: 10px;
}
.accordion__body--active {
  display: block;
  height: auto;
}

/*********************************
    モーダル
*********************************/
.modal {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
  z-index: 40;
}
.modal--active {
  opacity: 1;
  visibility: visible;
}
.modal::backdrop {
  background: rgba(0, 0, 0, 0.6);
}

.modal__box {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  width: min(400px, 80%);
  max-height: 80%;
  background-color: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.1), 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
  transform: translateY(-50%) translateX(-50%);
}

.modal__box-inner {
  display: flex;
  flex-direction: column;
  padding: 20px;
  gap: 20px;
  overflow-y: auto;
}

.modal__header {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal__body {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal__footer {
  display: flex;
  justify-content: center;
}

.modal__close {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -10px;
  right: -10px;
  width: 30px;
  height: 30px;
  background-color: #1a1a1a;
  border-radius: 100px;
  transition: 0.1s;
}
.modal__close:hover, .modal__close:active, .modal__close:focus {
  background-color: #b3b3b3;
}

.modal__close-icon {
  color: #FFFFFF;
  font-size: 16px;
}

/*********************************
    ローディング
*********************************/
.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #f2f2f2;
  z-index: 50;
  opacity: 0;
  visibility: hidden;
}
.loading--active {
  opacity: 1;
  visibility: visible;
}

.loading__inner {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loading__icon {
  font-size: 10px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  animation: load5 1.1s infinite ease;
  transform: translateZ(0);
}

@keyframes load5 {
  0%, 100% {
    box-shadow: 0em -2.6em 0em 0em #FFFFFF, 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2), 2.5em 0em 0 0em rgba(51, 51, 51, 0.2), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.2), 0em 2.5em 0 0em rgba(51, 51, 51, 0.2), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.2), -2.6em 0em 0 0em rgba(51, 51, 51, 0.5), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.7);
  }
