    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.7), 1.8em -1.8em 0 0em #FFFFFF, 2.5em 0em 0 0em rgba(51, 51, 51, 0.2), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.2), 0em 2.5em 0 0em rgba(51, 51, 51, 0.2), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.2), -2.6em 0em 0 0em rgba(51, 51, 51, 0.2), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.5);
  }
  25% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.5), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.7), 2.5em 0em 0 0em #FFFFFF, 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.2), 0em 2.5em 0 0em rgba(51, 51, 51, 0.2), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.2), -2.6em 0em 0 0em rgba(51, 51, 51, 0.2), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2);
  }
  37.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.2), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.5), 2.5em 0em 0 0em rgba(51, 51, 51, 0.7), 1.75em 1.75em 0 0em #FFFFFF, 0em 2.5em 0 0em rgba(51, 51, 51, 0.2), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.2), -2.6em 0em 0 0em rgba(51, 51, 51, 0.2), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2);
  }
  50% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.2), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2), 2.5em 0em 0 0em rgba(51, 51, 51, 0.5), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.7), 0em 2.5em 0 0em #FFFFFF, -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.2), -2.6em 0em 0 0em rgba(51, 51, 51, 0.2), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2);
  }
  62.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.2), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2), 2.5em 0em 0 0em rgba(51, 51, 51, 0.2), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.5), 0em 2.5em 0 0em rgba(51, 51, 51, 0.7), -1.8em 1.8em 0 0em #FFFFFF, -2.6em 0em 0 0em rgba(51, 51, 51, 0.2), -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2);
  }
  75% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.2), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2), 2.5em 0em 0 0em rgba(51, 51, 51, 0.2), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.2), 0em 2.5em 0 0em rgba(51, 51, 51, 0.5), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.7), -2.6em 0em 0 0em #FFFFFF, -1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2);
  }
  87.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(51, 51, 51, 0.2), 1.8em -1.8em 0 0em rgba(51, 51, 51, 0.2), 2.5em 0em 0 0em rgba(51, 51, 51, 0.2), 1.75em 1.75em 0 0em rgba(51, 51, 51, 0.2), 0em 2.5em 0 0em rgba(51, 51, 51, 0.2), -1.8em 1.8em 0 0em rgba(51, 51, 51, 0.5), -2.6em 0em 0 0em rgba(51, 51, 51, 0.7), -1.8em -1.8em 0 0em #FFFFFF;
  }
}
.loading__progress-bar {
  position: relative;
  width: 100px;
  height: 8px;
  margin-top: 60px;
  border: 1px solid #cccccc;
  border-radius: 100px;
}

.loading__bar {
  display: block;
  width: 0;
  height: 100%;
  background-color: #666666;
  border-radius: 100px;
}

/*********************************
    スライドショー
*********************************/
.slideshow__track {
  height: calc(100vh - 4rem);
}
@media screen and (max-width: 991.98px) {
  .slideshow__track {
    height: calc(100vh - 3rem);
  }
}

.slideshow__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media screen and (max-width: 767.98px) {
  .slideshow__img--pc {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .slideshow__img--sp {
    display: none;
  }
}

.slideshow__arrow {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 100%;
  transition: 0.1s;
  z-index: 1;
}
.slideshow__arrow:hover, .slideshow__arrow:active, .slideshow__arrow:focus {
  background-color: rgba(255, 255, 255, 0.1019607843);
}
.slideshow__arrow:hover .slideshow__icon, .slideshow__arrow:active .slideshow__icon, .slideshow__arrow:focus .slideshow__icon {
  color: #FFFFFF;
}
.slideshow__arrow--prev {
  left: max(0px, (100% - 1200px) / 2);
}
.slideshow__arrow--next {
  right: max(0px, (100% - 1200px) / 2);
}
@media screen and (max-width: 767.98px) {
  .slideshow__arrow {
    width: 30px;
  }
}

.slideshow__arrow-icon {
  color: rgba(255, 255, 255, 0.5019607843);
  font-size: 24px;
  transition: 0.1s;
}
@media screen and (max-width: 767.98px) {
  .slideshow__arrow-icon {
    width: 18px;
  }
}

.slideshow__footer {
  position: absolute;
  display: flex;
  left: 0;
  right: 0;
  bottom: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  z-index: 1;
}
@media screen and (max-width: 767.98px) {
  .slideshow__footer {
    bottom: 10px;
  }
}

.slideshow__toggle {
  display: flex;
}

.slideshow__toggle-play, .slideshow__toggle-pause {
  line-height: 1;
