  gap: 10px;
}

.dropdown > li > ul > li > a {
  justify-content: space-between;
  height: 3rem;
  padding: 0 10px 0 20px;
  gap: 10px;
  white-space: nowrap;
}

.dropdown > li > ul > li > ul > li > a {
