/*********************************
     WPドロワー
*********************************/
/* drawer */
.drawer {
  position: absolute;
  top: 3rem;
  right: 0;
  width: min(200px, 60vw);
  height: calc(100vh - 3rem);
  padding: 20px 0;
  background-color: rgba(255, 255, 255, 0.9490196078);
  border-top: 1px solid #e6e6e6;
  box-shadow: 0px 9px 8px 1px rgba(0, 0, 0, 0.1), 0px 5px 5px 0px rgba(0, 0, 0, 0.3);
  transform: translateX(100%);
  transform-origin: right;
  opacity: 0;
  visibility: hidden;
}
.drawer--active {
  opacity: 1;
  visibility: visible;
}

.drawer > li > ul,
.drawer > li > ul > li > ul {
  position: absolute;
  top: -1px;
  left: 0px;
  width: min(200px, 60vw);
  height: calc(100vh - 3rem);
  padding: 20px 0;
  background-color: rgba(255, 255, 255, 0.9490196078);
  border-top: 1px solid #e6e6e6;
  border-left: 1px solid #e6e6e6;
  transform: translateX(100%);
  transform-origin: right;
  opacity: 0;
  visibility: hidden;
}

.drawer > li > ul {
  left: -1px;
}

.drawer > li > ul > li > ul {
  left: -2px;
}

.drawer > li > a,
.drawer > li > ul > a,
.drawer > li > ul > li > a,
.drawer > li > ul > li > ul > a,
.drawer > li > ul > li > ul > li > a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 3rem;
  padding: 0 20px;
  gap: 10px;
  color: #1a1a1a;
  font-size: 1.125rem;
  line-height: 1.4;
  text-decoration: none;
  transition: 0.1s;
  cursor: pointer;
}
.drawer > li > a:hover, .drawer > li > a:active, .drawer > li > a:focus,
.drawer > li > ul > a:hover,
.drawer > li > ul > a:active,
.drawer > li > ul > a:focus,
.drawer > li > ul > li > a:hover,
.drawer > li > ul > li > a:active,
.drawer > li > ul > li > a:focus,
.drawer > li > ul > li > ul > a:hover,
.drawer > li > ul > li > ul > a:active,
.drawer > li > ul > li > ul > a:focus,
.drawer > li > ul > li > ul > li > a:hover,
.drawer > li > ul > li > ul > li > a:active,
