/*********************************
    WPページャー
*********************************/
.nav-links {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
}

.nav-links .page-numbers,
.nav-links .prev,
.nav-links .next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: #666666;
  border: 1px solid transparent;
  color: #FFFFFF;
  line-height: 1.4;
  text-decoration: none;
  transition: 0.1s;
}
.nav-links .page-numbers:hover, .nav-links .page-numbers:active, .nav-links .page-numbers:focus,
.nav-links .prev:hover,
.nav-links .prev:active,
.nav-links .prev:focus,
.nav-links .next:hover,
.nav-links .next:active,
.nav-links .next:focus {
  background-color: #333333;
}
.nav-links .page-numbers:first-child,
.nav-links .prev:first-child,
.nav-links .next:first-child {
  border-radius: 4px 0 0 4px;
}
.nav-links .page-numbers:last-child,
.nav-links .prev:last-child,
.nav-links .next:last-child {
  border-radius: 0 4px 4px 0;
}
.nav-links .page-numbers.current, .nav-links .page-numbers.dots,
.nav-links .prev.current,
.nav-links .prev.dots,
.nav-links .next.current,
.nav-links .next.dots {
  background-color: #FFFFFF;
  border-color: #666666;
  color: #666666;
  pointer-events: none;
}
.nav-links .page-numbers.dots,
.nav-links .prev.dots,
.nav-links .next.dots {
  border-color: transparent;
}

/*********************************
     WPラジオボタン
*********************************/
.wp_form-radio {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.wp_form-radio .wpcf7-list-item {
  margin: 0;
  line-height: 1;
}

.wp_form-radio > span > label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.wp_form-radio > span > label > input {
  width: 0;
  height: 0;
}
.wp_form-radio > span > label > input:focus + .form-radio__icon {
  outline: 2px solid #0877D7;
}
.wp_form-radio > span > label > input:checked + .form-radio__icon {
  border-color: #0017C1;
}
.wp_form-radio > span > label > input:checked + .form-radio__icon::before {
  display: block;
}

.wp_form-radio .wpcf7-list-item-label {
  line-height: 1.4;
}

/*********************************
     WPチェックボックス
*********************************/
.wp_form-checkbox {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.wp_form-checkbox .wpcf7-list-item {
  margin: 0;
  line-height: 1;
}

.wp_form-checkbox > span > label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.wp_form-checkbox > span > label > input {
  width: 0;
  height: 0;
}
.wp_form-checkbox > span > label > input:focus + .form-checkbox__icon {
  outline: 2px solid #0877D7;
}
.wp_form-checkbox > span > label > input:checked + .form-checkbox__icon {
  border-color: #0017C1;
}
.wp_form-checkbox > span > label > input:checked + .form-checkbox__icon::before {
  display: block;
}

.wp_form-checkbox .wpcf7-list-item-label {
  line-height: 1.4;
}

/*********************************
     WPフォーム
*********************************/
/* レイアウト修正 */
.wpcf7-form-control-wrap {
  width: 100%;
}
.wpcf7-form-control-wrap:has(.form-text--name) {
  width: auto;
}

.wpcf7-spinner {
  display: none;
}

/* エラーメッセージ */
.wpcf7-not-valid-tip {
  margin-top: 5px;
  color: #EC0000;
  font-size: 0.875rem;
  line-height: 1.4;
}

.wpcf7 form .wpcf7-response-output {
  margin-left: 0;
  margin-right: 0;
  padding-left: 0;
  padding-right: 0;
  border: none;
  color: #EC0000;
  font-size: 0.875rem;
  line-height: 1.4;
}

/* ContactForm7で送信ボタンを押したときにエラーメッセージが出るようにする */
.wpcf7-form.hide_error_message .wpcf7-not-valid-tip {
  display: none;
}

